import { ChangeEvent, Component, useState, MouseEvent } from "react";
import Dropdown from "../../_shared/components/app-dropdown/app-dropdown";
import './task-assignment.scss';
import SegmentControl from "../../_shared/components/segment/segment";
import { TaskType } from "../../enum/order.enum";
import FactCheckOutlined from "@mui/icons-material/FactCheckOutlined";
import FlagOutlinedIcon from '@mui/icons-material/FlagOutlined';
import Inventory2OutlinedIcon from '@mui/icons-material/Inventory2Outlined';
import AppTableView from "../../_shared/components/app-table-view/app-table-view";
import AppLoaderService from "../../_service/app-loader-service";
import { requestType } from "../../enum/ReportEnum";
import UserService from "../../_service/user.service";
import { logger } from "../../_core/logger.service";
import { User } from "../../_models/user";
import { Button } from "@mui/material";
import Chips from "../../_shared/components/app-chips/app-chips";

class TaskAssignment extends Component<any, any> {
    constructor(props: any) {
        super(props);
        this.state = {
            dropdownOptions: [],
            chipsData: [
                { status: TaskType.documentsCenter, label: 'Document Center List', count: 0 },
                { status: TaskType.flaggedReport, label: 'Flagged Report List', count: 0 },
                { status: TaskType.shipmentSupport, label: 'Order Support List', count: 0 }
            ],
            segments: [
                { name: 'Documents center', status: TaskType.documentsCenter, type: requestType.documentCenter, icon: <FactCheckOutlined />, isSelected: true },
                { name: 'Flagged report', status: TaskType.flaggedReport, type: requestType.report, icon: <FlagOutlinedIcon />, isSelected: false },
                { name: 'Shipment support', status: TaskType.shipmentSupport, type: requestType.order, icon: <Inventory2OutlinedIcon />, isSelected: false }
            ],
            currentTab: { name: 'Documents center', status: TaskType.documentsCenter, isSelected: true },
            columns: [
                { id: 1, numeric: false, disablePadding: true, label: 'User Name' },
                { id: 2, numeric: true, disablePadding: false, label: 'Assigned' },
                { id: 3, numeric: true, disablePadding: false, label: 'Due By' }
            ],
            rows: [],
            selectedUser: 0,
            selected: []
        };

    }

    fetchDropdownOptions = () => {
        UserService.getSupportUsers()
            .then((res: any) => {
                console.log(res.users)
                this.setState((prevState: any) => ({
                    ...prevState,
                    dropdownOptions: res.users?.map((e: User) => { return { value: e.id, displayName: e.displayName ?? e.name } })
                }), () => {
                    AppLoaderService.showLoader(false);
                });
            })
            .catch((err: any) => {
                logger.debug(err);
                AppLoaderService.showLoader(false);
            })
    };
    componentDidMount() {
        this.fetchDropdownOptions();
    }
    fetchReportList(searchTerm?: string) {
        AppLoaderService.showLoader(true);
        const status = 0;
        const type = this.state.currentTab.type ?? requestType.documentCenter;
        UserService.getReportList({ status, type, searchTerm, userId: this.state.selectedUser })
            .then((res: any) => {
                this.setState((prevState: any) => ({
                    ...prevState,
                    rows: res.result ?? []
                }), () => {
                    AppLoaderService.showLoader(false);
                });
            })
            .catch((err: any) => {
                logger.debug(err);
                AppLoaderService.showLoader(false);
            })
    }

    handleSelect = (selectedOption: any) => {
        this.setState((prevState: any) => ({
            ...prevState,
            selectedUser: selectedOption
        }), () => {
            this.fetchReportList()
        });
    }
    handleTransferToSelect = (selectedOption: any) => {
        console.log(selectedOption)
    }

    segmentSelected = (i: number) => {
        this.setState((prevState: any) => ({
            segments: prevState.segments.map((obj: any, index: number) => ({
                ...obj, isSelected: i === index
            })),
            currentTab: prevState.segments[i],
        }), () => {
            this.fetchReportList();
        })
    }

    showReportee = () => {
    }
    onTextChange = (searchTerm: string) => {
        if (searchTerm.trim() !== "") {
            this.fetchReportList(searchTerm);
        } else {
            this.fetchReportList();
        }
    }
    onSelectAllClick = () => {
        console.log('select all clicked')
    }
    onSelectClick = (id: number) => {
        const selectedIndex = this.state.selected.indexOf(id);
        let newSelected: readonly number[] = [];
        let isUnCheck = false;
        if (selectedIndex === -1) {
            newSelected = newSelected.concat(this.state.selected, id);
        } else if (selectedIndex === 0) {
            newSelected = newSelected.concat(this.state.selected.slice(1));
            isUnCheck = true;
        } else if (selectedIndex === this.state.selected.length - 1) {
            newSelected = newSelected.concat(this.state.selected.slice(0, -1));
            isUnCheck = true;
        } else if (selectedIndex > 0) {
            newSelected = newSelected.concat(
                this.state.selected.slice(0, selectedIndex),
                this.state.selected.slice(selectedIndex + 1),
            );
            isUnCheck = true;
        }
        const prevChipsData = this.state.chipsData;
        if (this.state.currentTab.status === TaskType.documentsCenter) {
            prevChipsData
        }
        this.setState((prevState: any) => ({
            segments: prevState.segments.map((obj: any, index: number) => ({
                ...obj,
            })),
            selected: newSelected,
            chipsData: this.state.chipsData.map((data: any) => {
                const newData = { ...data }; // Create a copy of the original object
                this.increaseCountIfNeeded(newData, isUnCheck); // Modify the count based on the condition
                return newData; // Return the modified object
            })
        }), () => {
        })
    }
    increaseCountIfNeeded = (data: any, isUnCheck: boolean) => {
        if (this.state.currentTab.status === data.status) {
            if (isUnCheck) { data.count = data.count - 1 } else { data.count += 1 };
        }
    };

    render() {
        const { dropdownOptions } = this.state;

        return (
            <>
                <div className="landing-page-task">
                    <div className="task-container">
                        <p>Transfer from:</p>
                        <div className="justify-spacebetween">
                            <div className="dropdown">
                                <Dropdown options={dropdownOptions} onSelect={this.handleSelect}></Dropdown>
                            </div>
                            <div className=' report_button_container'>
                                <div className="report_button">
                                    <Button className='button' onClick={this.showReportee}>Show Reportee</Button>
                                </div>
                            </div>
                        </div>
                    </div>
                    {this.state.selectedUser != 0 &&
                        <>
                            <div className="segment-view">
                                <SegmentControl segments={this.state.segments} onSegmentClick={(i) => this.segmentSelected(i)} displayLabel={true}></SegmentControl>
                            </div>
                            <div className="task-assignment-landing">
                                <AppTableView numSelected={0}
                                    selected={this.state.selected}
                                    onTextChange={(i) => this.onTextChange(i)}
                                    onSelectClick={(i) => this.onSelectClick(i)}
                                    onSelectAllClick={(i) => this.onSelectAllClick()} order={"asc"} orderBy={""} rowCount={0} columns={this.state.columns} rows={this.state.rows}></AppTableView>
                                <div className="transfer-to-view">
                                    <div className="selected-list-view">
                                        <div className="header-label">
                                            <label> Selected List</label>
                                        </div>
                                        <div>
                                            <Chips chipsData={this.state.chipsData}></Chips>
                                        </div>
                                    </div>
                                    <div>
                                        <p>Transfer to:</p>
                                        <div className="trasfer-to-dd">
                                            <Dropdown options={dropdownOptions} onSelect={this.handleTransferToSelect}></Dropdown>
                                        </div>
                                    </div>
                                </div>
                            </div></>
                    }

                </div>
            </>
        )
    }
}

export default TaskAssignment;
