import React, { ChangeEvent, useEffect, useState } from 'react';
import { Chip, Stack, colors, styled } from '@mui/material';
import { ChipData } from '../../../_models/app-common-model';
import './app-chips.scss'


interface ChipsProps {
    chipsData: ChipData[];
}

const Chips: React.FC<ChipsProps> = (props: ChipsProps) => {
    const handleDelete = () => {

    }
    const CustomChip = styled(Chip)(({ theme }) => ({
        color: 'var(--primary-color)',
        fontFamily: 'JosefinSansSemiBold',
        fontSize: '18px'
    }));
    return (
        <>
            <Stack direction="row" spacing={1}>
                {props.chipsData.map((data, index) => {
                    if (data.count && data.count > 0) {
                        return (
                            <CustomChip
                                key={index}
                                label={(data.count ?? "") + " " + data.label}
                                onDelete={() => handleDelete()}
                                variant="outlined"
                            />
                        );
                    }
                })}
            </Stack>
        </>
    )
};

export default Chips;
