import { Avatar, Box, Checkbox, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TableSortLabel } from "@mui/material";
import { visuallyHidden } from '@mui/utils';
import React from "react";
import './app-table-view.scss';
import SearchBar from "../search-bar/search-bar";
import { HeaderCell, TableList } from "../../../_models/app-common-model";

type Order = 'asc' | 'desc';
interface AppTableViewProps {
    numSelected: number;
    onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
    onSelectClick: (id: number) => void;
    order: Order;
    orderBy: string;
    rowCount: number;
    columns: HeaderCell[];
    rows: TableList[];
    selected: number[];
    onTextChange: (searchTerm: string) => void;
}

const AppTableView = (props: AppTableViewProps) => {
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(5);
    const [dense, setDense] = React.useState(false);
    // const [selected, setSelected] = React.useState<readonly number[]>([]);
    // Avoid a layout jump when reaching the last page with empty rows.
    const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - props.rows.length) : 0;
    const { onSelectAllClick, order, orderBy, numSelected, rowCount, columns, rows, onTextChange, onSelectClick
    } = props;
    const formatDateAssigned = (assignedDate: Date) => {
        const today = new Date();
        const diffTime = today.getTime() - assignedDate.getTime();
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

        if (diffDays === 0) {
            return "Today";
        } else {
            return `${diffDays} ${diffDays === 1 ? ' day' : 'days'} ago`;
        }
    };
    const formatDateDue = (dueDate: Date) => {
        const today = new Date();
        const diffTime = dueDate.getTime() - today.getTime();
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));

        if (diffDays === 0) {
            return "Today";
        } else {
            return `${Math.abs(diffDays)} ${Math.abs(diffDays) === 1 ? 'day' : 'days'} to go`;
        }
    };
    const createSortHandler =
        (property: keyof TableList) => (event: React.MouseEvent<unknown>) => {
            // onRequestSort(event, property);
        };
    const isSelected = (id: number) => props.selected.indexOf(id) !== -1;
    const onChangeHandler = (ev: string) => {
        onTextChange(ev)
    }
    return (
        <>
            <div className="table-container">
                <div className="table-title">
                    <div className="title-text">Document Center List</div>
                    <div className="search-bar-container">
                        <SearchBar changeHandler={onChangeHandler} />
                    </div>
                </div>
                <TableContainer style={{ maxHeight: 'calc(100% - 60px)' }}>
                    <Table>
                        <TableHead>
                            <TableRow>
                                <TableCell padding="checkbox">
                                    <Checkbox
                                        color="primary"
                                        indeterminate={numSelected > 0 && numSelected < rowCount}
                                        checked={rowCount > 0 && numSelected === rowCount}
                                        onChange={onSelectAllClick}
                                        inputProps={{
                                            'aria-label': 'select all desserts',
                                        }}
                                    />
                                </TableCell>
                                {columns.map((headCell) => (
                                    <TableCell
                                        key={headCell.id}
                                        align={headCell.numeric ? 'right' : 'left'}
                                        padding={headCell.disablePadding ? 'none' : 'normal'}
                                    // sortDirection={orderBy === headCell.id ? order : false}
                                    >
                                        <TableSortLabel
                                        // active={orderBy === headCell.id}
                                        // direction={orderBy === headCell.id ? order : 'asc'}
                                        // onClick={createSortHandler(headCell.id)}
                                        >
                                            <label className="header-label">{headCell.label}</label>
                                            {/* {orderBy === headCell.id ? (
                                        <Box component="span" sx={visuallyHidden}>
                                            {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                                        </Box>
                                    ) : null} */}
                                        </TableSortLabel>
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {props.rows.map((row, index) => {
                                const isItemSelected = isSelected(row.id);
                                const labelId = `enhanced-table-checkbox-${index}`;
                                return (
                                    <TableRow
                                        hover
                                        onClick={(event) => onSelectClick(row.id)}
                                        role="checkbox"
                                        aria-checked={isItemSelected}
                                        tabIndex={-1}
                                        key={row.id}
                                        selected={false}
                                        sx={{ cursor: 'pointer', width: '100%' }}
                                    >
                                        <TableCell padding="checkbox">
                                            <Checkbox
                                                color="primary"
                                                checked={isItemSelected}
                                                inputProps={{
                                                    'aria-labelledby': labelId,
                                                }}
                                            // onClick={(event) => onSelectClick(row.id)}
                                            />
                                        </TableCell>
                                        <TableCell
                                            component="th"
                                            id={labelId}
                                            scope="row"
                                            padding="none"
                                        >
                                            <div className="table-username-cell">
                                                <div className="table-profile-image">
                                                    <Avatar src={row.profile_pic}></Avatar>
                                                </div>
                                                {row.name}
                                            </div>
                                        </TableCell>
                                        <TableCell align="right" style={{ color: 'var(--quinary-color)' }}> {row.assignedDate ? formatDateAssigned(new Date(row.assignedDate)) : ''}</TableCell>
                                        <TableCell align="right" style={{ color: 'var(--quinary-color)' }} > {row.dueDate ? formatDateDue(new Date(row.dueDate)) : ''}</TableCell>
                                    </TableRow>
                                );
                            })}
                            {emptyRows > 0 && (
                                <TableRow
                                    style={{
                                        height: (dense ? 33 : 53) * emptyRows,
                                    }}
                                >
                                    <TableCell colSpan={6} />
                                </TableRow>
                            )}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
        </>
    );
}

export default AppTableView;